import {
	mapState,
	mapMutations
} from 'vuex'
import request from '../../common/requesst/interface.js'
import {getImgUrl} from '../../common/fileUtil.js'
export default {
	data() {
		return {
			edit: false,
			user: {
				birthday: '',
				createdTime: '',
				departmentName: '',
				duties: '',
				email: '',
				introduction: '',
				name: ''
			}
		}
	},
	computed: {
		...mapState(['personInfo'])
	},
	methods: {
		...mapMutations(['setPersonInfo']),
		putForm: function() {
			request({
				url: '/user',
				method: 'PUT',
				data: {
					duties: this.user.duties,
					introduction: this.user.introduction,
					name: this.user.name
				}
				}).then( res => {
					if (res.data.code === 1000) {
						this.edit = !this.edit
						uni.showToast({
							title: '更新成功'
						});
						getUserInfo().then( res => {
							if (res.data.code === 1000) {
								res.data.data.personalImage = getImgUrl(res.data.data.personalImage)
								this.setPersonInfo(res.data.data)
							}
						})
					}
			})
		}
	},
	onLoad() {
		this.user = this.personInfo
	}
}
